hr {
	margin: 80px auto 40px;
	width: 80%;
	position: relative;
	overflow: hidden;
	height: 20px;
	border: none !important;
}

hr::before {
}

hr::after {
	content: '';
	display: block;
	margin: -25px auto 0;
	width: 100%;
	height: 25px;
	border-radius: 125px/12px;
	box-shadow: 0 0 16px rgba( 0, 0, 0, 0.2 ), 0 0 5px rgba( 0, 0, 0, 0.4 );
}
