@media only screen and ( max-width: 600px ) {
	.wp-block-navigation__container {
		width: 100%;
		gap: 0 !important;
	}

	.wp-block-navigation .has-child .wp-block-navigation__submenu-container {
		display: none;
	}

	.wp-block-navigation .has-child:hover .wp-block-navigation__submenu-container {
		display: block;
	}

	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content {
		align-items: flex-start;
	}

	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content .wp-block-navigation-item,
	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content .wp-block-navigation__container,
	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content .wp-block-page-list {
		align-items: flex-start;
	}

	.wp-block-navigation__container > li > a {
		font-size: 1rem;
		font-weight: 700;
	}

	.wp-block-navigation-submenu {
		padding-top: 0;
	}

	.wp-block-navigation__container > .has-child {
		width: 100%;
	}

	.wp-block-navigation__container > li.has-child::after {
		background: url( 'data:image/svg+xml;base64,CjxzdmcgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIiBoZWlnaHQ9IjQ4IiB2aWV3Qm94PSIwIC05NjAgOTYwIDk2MCIgd2lkdGg9IjQ4Ij48cGF0aCBkPSJNNDgwLTM0NSAyNDAtNTg1bDQzLTQzIDE5NyAxOTggMTk3LTE5NyA0MyA0My0yNDAgMjM5WiIvPjwvc3ZnPg==' );
		background-repeat: no-repeat;
		content: '';
		height: 36px;
		width: 36px;
		right: 5px;
		top: 3px;
		position: absolute;
		background-size: 36px;
	}

	.wp-block-navigation-link {
		width: 100%;
	}

	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content .wp-block-navigation-item__content {
		width: 100%;
		padding: 10px;
	}

	.wp-block-navigation__responsive-container.is-menu-open .wp-block-navigation__responsive-container-content .has-child .wp-block-navigation__submenu-container {
		padding-top: 0;
		font-weight: 400;
	}
}

@media only screen and ( min-width: 600px ) {
	.wp-block-navigation .has-child .wp-block-navigation__submenu-container {
		display: block;
	}

	.wp-block-navigation-item__content {
		padding: 0.5rem 1rem;
	}

	.wp-block-navigation-item__content:hover {
		background-color: transparent !important;
	}

	.wp-block-navigation__container > li {
		transition: all 300ms ease-in-out;
	}

	.wp-block-navigation__container > li.current-menu-item::after {
		content: '';
		position: absolute;
		bottom: 0;
		height: 4px;
		transform-origin: 0% 0%;
		width: 100%;
		background: #a5a7a9;
	}

	.wp-block-navigation__container > li::after {
		content: '';
		position: absolute;
		width: 100%;
		height: 4px;
		bottom: 0;
		transform: scaleX( 0 );
		background: transparent;
		transition: 300ms;
		pointer-events: none;
	}

	.wp-block-navigation__container > li:hover::after {
		content: '';
		position: absolute;
		bottom: 0;
		width: 100%;
		transform: scaleX( 1 );
		height: 4px;
		background: var( --wp--preset--color--primary );
		transition: 300ms;
		pointer-events: none;
	}

	ul.wp-block-navigation-submenu {
		border: none;
		box-shadow: 0px 2px 6px rgba( 0, 0, 0, 0.2 );
	}

	.wp-block-navigation-submenu .wp-block-navigation-link:hover {
		color: #fff;
		background-color: var( --wp--preset--color--primary );
	}
}
