.wp-block-latest-posts {
	img {
		aspect-ratio: 16/9;
		object-fit: cover;
	}

	li {
		transition: all 300ms ease-in-out;

		&:hover {
			background: #fff;
			transform: scale( 1.05 );
			box-shadow: 0 0 15px rgba( 0, 0, 0, 0.1 );
		}
	}

	&__post-title,
	&__post-excerpt,
	time {
		padding-left: 1rem;
		padding-right: 1rem;
		display: block;
	}

	&__post-title {
		font-size: 1.25rem;
		font-weight: 600;
	}

	time {
		font-size: 0.75rem;
		color: #a5a7a9;
	}
}
