.wp-block-search__input {
	position: relative;
	width: 100%;
	cursor: pointer;
	border-radius: 99px;
	font-family: var( --wp--preset--font-family--montserrat );
	color: transparent;
}

.wp-block-search__input:focus {
	outline: 2px solid var( --wp--preset--color--primary ) !important;
	color: black;
}

/* clears the ‘X’ from Internet Explorer */
input[type='search']::-ms-clear {
	display: none;
	width: 0;
	height: 0;
}
input[type='search']::-ms-reveal {
	display: none;
	width: 0;
	height: 0;
}
/* clears the ‘X’ from Chrome */
input[type='search']::-webkit-search-decoration,
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-results-button,
input[type='search']::-webkit-search-results-decoration {
	display: none;
}

@media only screen and ( max-width: 600px ) {
	.wp-block-search__input::placeholder {
		opacity: 1;
	}

	.wp-block-search {
		width: 100%;
		order: -1;
	}

	.wp-block-search__inside-wrapper {
		position: relative;
		width: 100%;
	}

	.wp-block-search__inside-wrapper::before {
		position: absolute;
		content: '';
		background-image: url( 'data:image/svg+xml;base64,' );
		background-repeat: no-repeat;
		height: 100%;
		width: 2rem;
		right: 5px;
		cursor: pointer;
		background-size: 25px;
		background-position: center;
		z-index: 1;
	}
}

@media only screen and ( min-width: 600px ) {
	.wp-block-search__input {
		min-width: 2rem;
		background: transparent;
		border: none;
		position: relative;
		width: 100%;
		cursor: pointer;
	}

	.wp-block-search__input::placeholder {
		opacity: 0;
	}

	.wp-block-search__inside-wrapper {
		position: relative;
		width: 2rem;
		border-radius: 99px;
		transition: width 300ms ease-in-out;
	}

	.wp-block-search__inside-wrapper:hover {
		background: var( --wp--preset--color--primary );
	}

	.wp-block-search__inside-wrapper::before {
		position: absolute;
		content: '';
		background-image: url( 'data:image/svg+xml;base64,' );
		background-repeat: no-repeat;
		height: 100%;
		width: 2rem;
		right: 0;
		cursor: pointer;
		background-size: 25px;
		background-position: center;
	}

	.wp-block-search__inside-wrapper:hover::before {
		background-image: url( 'data:image/svg+xml;base64,' );
	}

	.wp-block-search__inside-wrapper:focus-within::before {
		background-image: url( 'data:image/svg+xml;base64,' ) !important;
	}

	.wp-block-search__inside-wrapper:focus-within {
		width: 8rem;
		border: 1px solid #bbb;
		background: #fff;
	}

	.wp-block-search__input:focus {
		border: none;
		cursor: text;
		background: transparent;
		outline: none;
	}
}
