.wp-block-list {
	list-style: none;
	padding-left: 0;

	li {
		position: relative;
		padding-left: 1.5rem;

		&::before {
			content: '';
			display: block;
			margin: 0 auto;
			width: 12px;
			height: 12px;
			position: absolute;
			left: 0;
			top: 6px;
			background: var( --wp--preset--color--primary );
		}
	}
}
