.msn-card {
	position: relative;
}

.msn-card .msn-card-content {
	width: 90%;
	left: 50%;
	transform: translate( 0, -3rem );
	box-shadow: 0 0 15px rgba( 0, 0, 0, 0.2 );
}

.msn-card img {
	aspect-ratio: 16/9;
	object-fit: cover;
}

.msn-icon-card {
	position: relative;
	background-color: #eaecef;
	height: 100%;
}

.msn-icon-card i {
	background: #f7f9fb;
	border-radius: 999px;
	padding: 1rem;
	aspect-ratio: 1/1;
	margin-top: -5rem;
	border: 7px solid #eaecef;
	box-shadow: 0 0 0 5px #f7f9fb;
}

@media only screen and ( max-width: 600px ) {
	.msn-icon-card {
		margin-top: 5rem !important;
	}
}
